* {
  color: hsl(236, 100%, 87%);
  font-family: Rubik, sans-serif;
  font-size: 16px;
}

body {
  background-color: hsl(226, 43%, 10%);
  display: flex;
  flex-flow: column wrap;
  align-items: center;
  margin-top: 6.5rem;
  text-align: center;
}

.parent-box,
.child-box {
  border-radius: 12px;
  width: 15.625rem;
  text-align: left;
}

.parent-box {
  max-height: 15.625rem;
  position: relative;
  background-repeat: no-repeat;
  background-size: 31.6%;
  background-position: 91% -6px;
  margin: 1rem;
}

.child-box {
  max-height: 12.5rem;
  height: 80%;
  position: absolute;
  bottom: 0px;
  right: 0px;
  background-color: hsl(235, 46%, 20%);
  cursor: pointer;
  transition: background-color 0.6s ease;
}

/* .child-box:hover {
  background-color: #34397b;
} */

h3 {
  font-weight: 400;
  font-size: 1.1rem;
  color: white;
}

.top svg {
  fill: hsl(236, 100%, 87%);
  cursor: pointer;
  background-color: transparent;
  padding: 5px;
  border-radius: 50%;
  transition: fill 1s ease, background-color 1s ease;
}

.top svg:hover {
  fill: white;
  background-color: hsla(236, 100%, 79%, 0.589);
}

.container {
  padding: 10px 30px;
  display: flex;
  flex-flow: column wrap;
  height: 9.5rem;
  justify-content: space-between;
}

.top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0px;
}

h1 {
  font-size: 48px;
  margin: 0px;
  font-weight: 200;
  color: white;
}

.bottom p {
  margin: 0px;
  margin-top: 10px;
  padding-left: 1px;
  font-size: 13px;
  font-weight: 300;
}

.parent-person,
.child-person {
  border-radius: 12px;
  width: 15.625rem;
}

.parent-person {
  min-height: 31.25rem;
  background-color: hsl(235, 46%, 20%);
  position: relative;
  margin: 1rem;
}

.child-person {
  background-color: hsl(246, 80%, 60%);
  margin-bottom: 20px;
  height: 65%;
  text-align: left;
}

.child-person img {
  width: 70px;
  height: 70px;
  border: 4px solid white;
  padding: 0px;
  border-radius: 60%;
  margin-bottom: 20px;
}

.container-person {
  padding: 30px;
}

.child-person h1 {
  font-size: 2.5rem;
}

.child-person p {
  margin-bottom: 0px;
  font-size: smaller;
}

.selectors {
  display: flex;
  flex-flow: column wrap;
  align-content: flex-start;
  justify-content: space-around;
  height: 25%;
  margin: 30px;
}

.selector {
  display: block;
  border: none;
  background-color: transparent;
  text-align: left;
  color: hsl(235, 45%, 61%);
  cursor: pointer;
  transition: color 0.4s ease-in-out;
}

.selector:hover {
  color: white;
}

.active {
  color: white;
}

.grid-container {
  display: grid;
  grid-template-columns: 1fr 3fr;
  grid-template-rows: 100%;
  grid-template-areas: "person table";
}

.table {
  grid-area: table;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
}

@media (max-width: 1200px) {
  body {
    margin-top: 3rem;
  }

  .parent-box,
  .child-box,
  .child-person,
  .parent-person {
    width: 13.625rem;
  }

  .parent-box {
    height: 13.625rem;
  }

  .parent-person {
    min-height: 27rem;
  }
}

@media (max-width: 1000px) {
  body {
    margin: 0.2rem;
    margin-top: 2.2rem;
  }

  .grid-container {
    display: block;
  }

  .table {
    display: block;
  }

  .parent-box,
  .parent-person,
  .child-box,
  .child-person {
    width: 100%;
  }

  .parent-box,
  .parent-person {
    margin: 20px 0px;
  }

  .parent-box {
    background-size: 25%;
    background-position-y: -12px;
  }

  .child-box {
    height: 75%;
  }

  .container {
    height: auto;
    padding-top: 20px;
  }

  .bottom {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .parent-person {
    min-height: 200px;
    height: auto;
    padding-bottom: 1px;
  }

  .container-person {
    display: flex;
    justify-content: space-evenly;
  }

  .container-person img {
    margin-right: 20px;
  }

  .selectors {
    flex-flow: row wrap;
  }

  .selector {
    font-size: larger;
  }
}

@media (max-width: 500px) {
  body {
    margin: 12px;
  }

  .child-box,
  .child-person,
  .parent-box,
  .parent-person {
    min-width: unset;
    width: 100%;
  }

  .bottom h1 {
    font-size: 36px;
  }

  .child-box {
    height: auto;
    padding-bottom: 10px;
  }

  .parent-box {
    min-height: 11.4rem;
    height: auto;
  }
}

@media (max-width: 400px) {
  html {
    font-size: 14px;
  }

  .container-person h1 {
    font-size: 24px;
  }

  .container-person {
    align-items: center;
  }

  .container-person p {
    font-size: 12px;
  }

  .title {
    margin: 0px;
    padding: 0px;
  }

  .container-person img {
    width: 60px;
    height: 60px;
    margin: 0px 10px 0px 0px;
  }

  .bottom h1 {
    font-size: 32px;
  }

  .container {
    padding-left: 20px;
    padding-right: 20px;
  }
}
